import uniIcon from "@/components/uni/uni-icon/uni-icon.vue"
export default {
	data() {
		return {
			list: ['a', 'b', 'c', 'a', 'b', 'c'],
			c_index: 0,
			kehu: ''
		};
	},
	components: {
		uniIcon
	},
	onLoad() {  
		this.kehu=this.$api.json_cms.kehu
		this.list=this.$api.json_cms.kh_category
	},
	methods: {
		jump_to_detail() {
			uni.navigateTo({
				url: '../kedetail/kedetail'
			});
		},
		jump_tag_manage() {
			uni.navigateTo({
				url: '../tag_manage/tag_manage'
			});
		},
		num(index) {
			this.c_index = index
		},
		jump_xiang(id) {
			uni.navigateTo({
				url: '/pages/kedetail/kedetail?id=' + id,
			});
		}
	},
	onPullDownRefresh() {
		//this._load()
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 2000);
	}
}
